import React, {Fragment, useContext, useEffect, useState} from "react";
import EditMaster from "./EditMaster";
import InputMaster from "./InputMaster";
import {SERVER_URL} from "../../../../constants";
import {toast} from "react-toastify";
import axios from "axios";
import {Context} from "../../../../index";
import {Spinner} from "react-bootstrap";
import {observer} from "mobx-react-lite";

const ListMasters = observer(() => {
    const {DB} = useContext(Context)
    const [loading, setLoading] = useState(true)
    const deleteMaster = async (id) => {
        try {
            await fetch(SERVER_URL + `/masters/${id}`, {
                method: "DELETE"
            })
                .then(response => response.json())
                .then(data => toast(data));
            axios.get(SERVER_URL + `/masters`)
                .then(resp => DB.setMasters(resp.data))
        } catch (e) {
            toast("–ê—Ö–∞—Ö—Ö–∞ —Å–µ—Ä–≤–µ—Ä —É–ø–∞–ª")
        }
    }

    useEffect(() => {
        axios.get(SERVER_URL + `/masters`)
            .then(resp => DB.setMasters(resp.data))
            .finally(() => setLoading(false))
    }, [])

    if (loading) {
        return (
            <div>
                <Spinner animation={`grow`}/>
            </div>
        )
    }
    return (
        <Fragment>
            <h2 className="text-left mt-5">–°–ø–∏—Å–æ–∫ –º–∞—Å—Ç–µ—Ä–æ–≤</h2>
            <table className="table mt-5 text-justify">
                <thead>
                <tr>
                    <th scope="col">#</th>
                    <th scope="col">–ò–º—è</th>
                    <th scope="col">–†–µ–π—Ç–∏–Ω–≥</th>
                    <th scope="col">–§–æ—Ç–æ</th>
                    <th scope="col">–ò–∑–º–µ–Ω–∏—Ç—å</th>
                    <th scope="col">–£–¥–∞–ª–∏—Ç—å</th>
                </tr>
                </thead>
                <tbody>
                {
                    DB.masters.map(master => (
                        <tr key={master.master_id}>
                            <th scope="row"> {master.master_id}</th>
                            <td>{master.master_name}</td>
                            <td>{master.ranking}</td>
                            <td>{master.photo}</td>
                            <td><EditMaster master={master}/></td>
                            <td>
                                <button className="btn btn-danger"
                                        onClick={() => deleteMaster(master.master_id)}>–£–¥–∞–ª–∏—Ç—å
                                </button>
                            </td>
                        </tr>
                    ))
                }

                </tbody>
            </table>
            <InputMaster/>
        </Fragment>
    )
})
export default ListMasters